import React, { Component } from 'react';
import { connect } from 'react-redux';
import BlogItem from './BlogItem';
import Button from './Button';
import { BLOG_ADD_POST, BLOG_UPDATE_POST, BLOG_SHOW_ALL, BLOG_FILTER, BLOG_DELETE_POST } from '../constans';

const category = [
  { filter: BLOG_SHOW_ALL, label: "Все" },
  { filter: 'Commercial', label: "Commercial" },
  { filter: 'Gunit', label: "Gunit" },
  { filter: 'Animal', label: "Animal" }
]



class BlogList extends Component {
  handleClick = (id) => {
    this.props.deletePost(id);
  }

  handleUpdate = ({ id, name }) => {
    this.props.updatePost({ id, name });
  }

  handleCategory = (id) => {
    this.props.filterPost({ filter: id });
  }



  render() {
    return (
      <div>
        <div className="nav">

          {category.map(data =>
            <Button
              key={data.filter}
              filter={this.props.filter}
              handleCategory={this.handleCategory}
              data={data} />
          )}
        </div>
        <ul>
          {this.props.blog.map(post =>
            <BlogItem
              key={post.id}
              post={post}
              handleUpdate={this.handleUpdate}
              handleClick={this.handleClick} />
          )}
        </ul>
        <button className="button-add" onClick={this.props.addPost}>Добавить</button>

      </div>
    )
  }
}


const mapStateToProps = ({ blog }) => {
  if (blog.filter === BLOG_SHOW_ALL) {
    return { blog: blog.data, filter: blog.filter }
  } else
    return { blog: blog.data.filter(post => post.category === blog.filter), filter: blog.filter }
}

const mapDispatchToProps = (dispatch) => {
  return {
    addPost: () => dispatch({
      type: BLOG_ADD_POST,
      payload: {
        id: Date.now(),
        name: "Hapy New Year",
        category: "Commercial",
        photo: 'https://ulisse.design/wp-content/uploads/2018/10/%D0%B2%D0%B8%D0%B4-1-2-1000x700.jpg',
        year: 2019
      },
    }),
    deletePost: (id) => dispatch({
      type: BLOG_DELETE_POST,
      payload: { id }
    }),
    updatePost: ({ id, name }) => dispatch({
      type: BLOG_UPDATE_POST,
      payload: { id, name },
    }),
    filterPost: ({ filter }) => dispatch({
      type: BLOG_FILTER,
      payload: { filter }
    })
  }
}




export default connect(mapStateToProps, mapDispatchToProps)(BlogList);
