import React, { Component } from 'react'

export default class BlogItem extends Component {

  handleClick = () => {
    this.props.handleClick(this.props.post.id);
  }

  handleUpdate = () => {
    const post = this.props.post;
    let name = this.refs.input.value;
    this.props.handleUpdate({ id: post.id, name });
  }

  render() {
    const { name, category, year, photo } = this.props.post;
    return (
      <li>
        <div className="left">
          <img src={photo} alt="" />
        </div>
        <div className="right">
          <h1>{name}</h1>
          <span>{category}</span>
          <span className="year">{year}</span>
          <input ref="input" type="text" placeholder="Введите текст" />
          <button className="button-update" onClick={this.handleUpdate}>Обновить</button>
          <button className="button-delete" onClick={this.handleClick}>Удалить</button>
        </div>
      </li>
    )
  }
}

